.container{
    max-width: 100%;
    display: flex;
    flex-wrap:wrap;
}

.card {
    width: 200px;
    height: 140px;
    background-color: #333;
    border:1px solid #fff;
    border-radius: 10px;
    margin: 10px;
    box-shadow: 3px 3px 3px #131212,-2px -2px 2px #fff;
}
.card:hover{
    width: 210px;
    height: 145px;
    transition: .4s;
}
.card-header{
    color: #fff;
    font-size: inherit;
    font-weight: bold;
    padding: 10px;
    text-align: center;
}
.card-body{
    display: flex;
    justify-content: space-between;
    color: #fff;
    border-top: 2px solid #fff;
    padding: 10px;
    border-bottom: 2px solid #fff;
    border-radius: 3px;
    font-weight:bold;
    
}
.card-botton{
    padding: 5px 10px;
    display: flex;
    justify-content: space-between;
}
.card-botton button{
    margin-top: .25rem;
    padding: .5rem;
    font-size: inherit;
    font-weight: bold;
    border-radius: 5rem;
}
.card-botton button:hover{
    background-color: #333;
    color:#ccc;
    cursor: pointer;
}