.main{
    display: flex;
    flex-direction: column;
    background-color: #333;
    width: 22rem;
    border-radius: 2rem;
    margin: 0 auto;
    box-shadow: 2px 2px 3px #fff,-3px -3px 3px #000;
}

.main div input{
    width: 20rem;
    height: 2.5rem;
    margin: .75rem;
    font-size: inherit;
    border-radius: 5rem;
    padding: 0 .5rem;
}
.main button {
    margin: .75rem;
    padding: .5rem;
    font-size: inherit;
    font-weight: bold;
    border-radius: 5rem;
}

.main button:hover{
    background-color: #333;
    color:#ccc;
    cursor: pointer;
}
.main .mar{
    margin:0 0 0 .75rem;
}
.main .ival {
    color: red;
}
.main .val{
    color:green;
}