.main{
    display: flex;
    flex-direction: column;
    width: 22rem;
    border-radius: 2rem;
    margin: 20px auto;
    background-color: greenyellow;
    box-shadow: 2px 2px 3px #000,-3px -3px 3px #000;

}

.main div{
    margin: .75rem;
    padding: 0 .5rem;
}
.main div label{
    font-size: inherit;
    font-weight: bold;
    margin: 1rem;
}

.main div input{
    width: 18.5rem;
    height: 2.5rem;
    margin: .5rem;
    font-size: inherit;
    border-radius: 5rem;
    padding: 0 .5rem;
}
.main button {
    margin: .75rem;
    width: 10rem;
    padding: .5rem;
    font-size: inherit;
    font-weight: bold;
    border-radius: 5rem;
}

.main button:hover{
    background-color: #333;
    color:#ccc;
    cursor: pointer;
}