.container {
    display: flex;
    flex-direction: column;
    max-width: 1080px;
    margin: 0 auto;
    padding: 20px;
}
.navbar ul{
    display: flex;
    margin: 0;
    padding: 0;
    font-weight: bold;
   
}
.navbar li{
    list-style: none;
    padding: 5px;
    
}
.navbar li a{
    color: #fff;
}
.navbar li a:hover{
    color: #333;
}
.navbar li:hover{
    background-color: #fff;
   
}
.navbar a{
    text-decoration: none;
}
.bar{
    width: 100%;
    height: 2px;
    background-color: #fff;
}
