.container {
    display: flex;
   align-items: center;
   margin: 20px 0;
}
.main {
    background-color:#333;
    border: 1px solid #fff;
    box-shadow: 3px 3px 3px #333, -2px -2px 2px #fff;
    color: white;
    width: 22rem;
    border-radius: 2rem;
    margin: 0 auto;
    text-align: center;
}

.fright {
    float: right;
}
.fright button , .cart button , .order button {
    margin: .75rem;
    padding: .5rem;
    font-size: inherit;
    font-weight: bold;
    border-radius: 5rem;
}
.fright button:hover,.cart button:hover ,.order button:hover{
    background-color: #333;
    color:#ccc;
    cursor: pointer;
}